import React, { useState } from 'react';
import styled from 'styled-components';

const GameContainer = styled.div`
  width: 100vw;
  height: 100vh;
  background: #f5f5f5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
`;

const HomeButton = styled.button`
  position: absolute;
  bottom: 20px;
  right: 20px;
  background: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  z-index: 10;
  &:hover {
    background: #45a049;
  }
`;

const OmikujiBox = styled.div`
  width: 300px;
  height: 400px;
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  margin: 20px;
`;

const Result = styled.div`
  font-size: 48px;
  font-weight: bold;
  color: ${props => {
    switch(props.fortune) {
      case '': return '#ff0000';
      case '': return '#ff6b6b';
      case '': return '#ffa500';
      case '': return '#ffd700';
      case '': return '#4169e1';
      case '': return '#000080';
      default: return '#000';
    }
  }};
  margin: 20px 0;
`;

const Button = styled.button`
  background: #ff6b6b;
  color: white;
  border: none;
  padding: 15px 30px;
  border-radius: 25px;
  font-size: 18px;
  cursor: pointer;
  margin: 20px;
  transition: all 0.3s ease;
  &:hover {
    transform: scale(1.05);
    background: #ff5252;
  }
`;

const Message = styled.p`
  font-size: 18px;
  color: #666;
  text-align: center;
  margin: 10px 0;
`;

const fortunes = ['', '', '', '', '', ''];
const messages = {
  '': '',
  '': '',
  '': '',
  '': '',
  '': '',
  '': ''
};

const OmikujiGame = ({ onGameOver }) => {
  const [result, setResult] = useState(null);
  const [isShaking, setIsShaking] = useState(false);

  const drawOmikuji = () => {
    setResult(null);
    setIsShaking(true);
    setTimeout(() => {
      const fortune = fortunes[Math.floor(Math.random() * fortunes.length)];
      setResult(fortune);
      setIsShaking(false);
    }, 1000);
  };

  return (
    <GameContainer>
      <HomeButton onClick={() => onGameOver(result ? 1 : 0)}></HomeButton>
      <h1></h1>
      <OmikujiBox>
        {result ? (
          <>
            <Result fortune={result}>{result}</Result>
            <Message>{messages[result]}</Message>
          </>
        ) : (
          <Message></Message>
        )}
      </OmikujiBox>
      <Button 
        onClick={drawOmikuji}
        style={{
          transform: isShaking ? 'scale(0.95)' : 'scale(1)',
          transition: 'transform 0.1s ease'
        }}
      >
        {result ? '' : ''}
      </Button>
    </GameContainer>
  );
};

export default OmikujiGame; 