// 
const japaneseWords = [
  '', '', '', '', '', '', '', '', '', 
  '', '', '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', '',
  '', '', '', '', '', '', '', '', ''
];

const ShiritoriGame = ({ onGameOver }) => {
  const [currentWord, setCurrentWord] = useState('');
  const [inputWord, setInputWord] = useState('');
  const [wordHistory, setWordHistory] = useState([]);
  const [score, setScore] = useState(0);
  const [timeLeft, setTimeLeft] = useState(30);
  const [gameActive, setGameActive] = useState(false);
  const [gameStarted, setGameStarted] = useState(false);
  const [playerTurn, setPlayerTurn] = useState(true); // true: , false: CPU

  useEffect(() => {
    if (!gameStarted) return;

    const timer = setInterval(() => {
      setTimeLeft(prev => {
        if (prev <= 1) {
          setGameActive(false);
          onGameOver(score);
          return 0;
        }
        return prev - 1;
      });
    }, 1000);

    return () => clearInterval(timer);
  }, [gameStarted, score, onGameOver]);

  useEffect(() => {
    if (!playerTurn && gameActive && currentWord) {
      // CPU
      setTimeout(() => {
        const lastChar = currentWord.slice(-1);
        const possibleWords = japaneseWords.filter(word => 
          word.startsWith(lastChar) && 
          !wordHistory.includes(word) &&
          word.length >= 2
        );
        
        if (possibleWords.length > 0) {
          const cpuWord = possibleWords[Math.floor(Math.random() * possibleWords.length)];
          setCurrentWord(cpuWord);
          setWordHistory(prev => [...prev, cpuWord]);
          setPlayerTurn(true);
        } else {
          // CPU
          setGameActive(false);
          onGameOver(score + 50); // 
        }
      }, 1500);
    }
  }, [playerTurn, gameActive, currentWord, wordHistory, score, onGameOver]);

  const startGame = () => {
    const initialWord = japaneseWords[Math.floor(Math.random() * japaneseWords.length)];
    setCurrentWord(initialWord);
    setWordHistory([initialWord]);
    setScore(0);
    setTimeLeft(30);
    setGameActive(true);
    setGameStarted(true);
    setPlayerTurn(false); // CPU
    setInputWord('');
  };

  const submitWord = () => {
    if (!inputWord.trim() || !gameActive || !playerTurn) return;

    const lastChar = currentWord.slice(-1);
    const newWord = inputWord.trim();

    // 
    if (newWord.length < 2) {
      alert('2');
      return;
    }

    if (!newWord.startsWith(lastChar)) {
      alert(`${lastChar}`);
      return;
    }

    if (wordHistory.includes(newWord)) {
      alert('');
      return;
    }

    if (!japaneseWords.includes(newWord)) {
      alert('');
      return;
    }

    // 
    setCurrentWord(newWord);
    setWordHistory(prev => [...prev, newWord]);
    setScore(prev => prev + 10);
    setInputWord('');
    setPlayerTurn(false);
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      submitWord();
    }
  };

  const resetGame = () => {
    setGameStarted(false);
    setGameActive(false);
    setCurrentWord('');
    setInputWord('');
    setWordHistory([]);
    setScore(0);
    setTimeLeft(30);
    setPlayerTurn(true);
  };

  const getLastLetter = () => {
    return currentWord ? currentWord.slice(-1) : '';
  };

  return (
    <GameContainer>
      <HomeButton onClick={() => onGameOver(score)}></HomeButton>
      <GameArea>
        <Title></Title>
        <Score>: {score}</Score>
        <Timer timeLeft={timeLeft}>: {timeLeft}</Timer>
        
        {!gameStarted ? (
          <Button onClick={startGame}></Button>
        ) : (
          <>
            <CurrentWord>: {currentWord}</CurrentWord>
            <LastLetter>{getLastLetter()}</LastLetter>
            
            <PlayerTurn>
              {playerTurn ? '' : 'CPU...'}
            </PlayerTurn>
            
            {playerTurn && gameActive && (
              <InputContainer>
                <Input
                  type="text"
                  value={inputWord}
                  onChange={(e) => setInputWord(e.target.value)}
                  onKeyPress={handleKeyPress}
                  placeholder={`${getLastLetter()}`}
                  disabled={!playerTurn}
                />
                <Button onClick={submitWord} disabled={!inputWord.trim()}>
                  
                </Button>
              </InputContainer>
            )}
            
            <WordHistory>
              <strong>:</strong>
              {wordHistory.map((word, index) => (
                <WordItem key={index}>
                  {index + 1}. {word}
                </WordItem>
              ))}
            </WordHistory>
            
            {!gameActive && timeLeft === 0 && (
              <>
                <GameOver></GameOver>
                <Button onClick={resetGame}></Button>
              </>
            )}
          </>
        )}
      </GameArea>
    </GameContainer>
  );
};

export default ShiritoriGame; 