import React, { useState } from 'react';
import styled from 'styled-components';

const GameContainer = styled.div`
  width: 100vw;
  height: 100vh;
  background: #f7fbff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
`;

const HomeButton = styled.button`
  position: absolute;
  bottom: 20px;
  right: 20px;
  background: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  z-index: 10;
  &:hover {
    background: #45a049;
  }
`;

const Title = styled.h1`
  margin-bottom: 20px;
`;

const Stats = styled.div`
  color: #111;
  font-size: 22px;
  margin: 16px;
`;

const Input = styled.input`
  font-size: 22px;
  padding: 8px 16px;
  border: 2px solid #b3d8f7;
  border-radius: 6px;
  margin-right: 12px;
  outline: none;
`;

const Button = styled.button`
  background: #174ea6;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  margin: 10px;
  font-size: 18px;
  cursor: pointer;
  &:hover { background: #2196f3; }
`;

const GameOver = styled.div`
  background: rgba(0,0,0,0.85);
  color: #fff;
  padding: 30px;
  border-radius: 10px;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`;

const NumberGuessGame = ({ onGameOver }) => {
  const [answer, setAnswer] = useState(() => Math.floor(Math.random() * 100) + 1);
  const [guess, setGuess] = useState('');
  const [tries, setTries] = useState(0);
  const [hint, setHint] = useState('1100');
  const [gameOver, setGameOver] = useState(false);
  const [gameStarted, setGameStarted] = useState(false);

  const handleChange = (e) => {
    setGuess(e.target.value.replace(/[^0-9]/g, ''));
  };

  const handleGuess = () => {
    if (!guess) return;
    const num = parseInt(guess, 10);
    if (num < 1 || num > 100) {
      setHint('1100');
      return;
    }
    setTries(t => t + 1);
    if (num === answer) {
      setHint('');
      setGameOver(true);
      setTimeout(() => onGameOver(100 - tries * 5), 1000);
    } else if (num < answer) {
      setHint('');
    } else {
      setHint('');
    }
    setGuess('');
  };

  const handleRestart = () => {
    setAnswer(Math.floor(Math.random() * 100) + 1);
    setGuess('');
    setTries(0);
    setHint('1100');
    setGameOver(false);
    setGameStarted(true);
  };

  const handleExit = () => {
    onGameOver(0);
  };

  return (
    <GameContainer>
      <HomeButton onClick={() => onGameOver(tries)}></HomeButton>
      <Title></Title>
      <p>1100</p>
      <Stats></Stats>
      <Stats>: {tries}</Stats>
      <Stats>{hint}</Stats>
      {!gameOver ? (
        <div>
          <Input
            type="text"
            value={guess}
            onChange={handleChange}
            maxLength={3}
            disabled={!gameStarted}
            onKeyDown={e => e.key === 'Enter' && handleGuess()}
          />
          <Button onClick={handleGuess} disabled={!gameStarted}></Button>
        </div>
      ) : null}
      {!gameStarted || gameOver ? (
        <GameOver>
          <h2>{gameOver ? '' : ''}</h2>
          {gameOver && <p>: {tries}</p>}
          <Button onClick={handleRestart}></Button>
          <Button onClick={handleExit}></Button>
        </GameOver>
      ) : null}
    </GameContainer>
  );
};

export default NumberGuessGame; 